body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  text-align: center;
}

/* fonts
font-family: 'Oswald', sans-serif;
font-family: 'Permanent Marker', cursive;
font-family: 'Bangers', cursive; */

:root {
  --mainBlue: #2a2a72;
  --lightBlue: #009ffd;
  --mainWhite: #f3f3f3;
  --mainDark: #232528;
  --mainYellow: #ffa400;
  --transition: all 0.5s ease 0s;
}

body {
  min-width: 320px;
  overflow-x: hidden;
  font-family: 'Oswald', sans-serif !important;
  background: #f3f3f3 !important;
  background: var(--mainWhite) !important;
  color: #232528 !important;
  color: var(--mainDark) !important;
}

.text-title {
  font-family: 'Bangers', cursive;
  text-transform: uppercase;
  letter-spacing: 0.12em;
}
.text-blue {
  color: #2a2a72;
  color: var(--mainBlue);
}
.text-bright {
  color: #009ffd;
  color: var(--lightBlue);
}
.btn {
  cursor: pointer;
}
.btn-black {
  background-color: transparent;
  text-transform: capitalize;
  font-size: 0.8rem !important;
  color: #232528;
  color: var(--mainDark);
  border-radius: 0 !important;
  border: 0.2rem solid #232528 !important;
  border: 0.2rem solid var(--mainDark) !important
}
.btn-black:hover {
  background-color: #232528 !important;
  background-color: var(--mainDark) !important;
  color: #f3f3f3 !important;
  color: var(--mainWhite) !important;
}
.cart-icon {
  cursor: pointer;
  font-size: 1.2rem;
  color: #ffa400;
  color: var(--mainYellow);
}

.paypal-should-focus .paypal-button:focus,
.paypal-should-focus .paypal-button-card:focus {
  outline: none !important;
}

